.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width:100%;
    height: 90px;
    z-index:10;
}
.header-bg{
    background-color:#262435;
    transition: background-color 0.5s ease-in-out;
}
.nav-menu{
    display:flex;
    
}
.nav-menu li{
    padding: 0 0.8rem;
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}
.nav-menu li a:hover{
    color: #fff;
    border-bottom: 2px solid #fff;
}
.hamburger{
    display:none;
}

@media screen and (max-width:640px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width:100%;
        height:100vh;
        background-color:#262435 ;
        position:absolute;
        top:0;
        left:-100%;
        z-index: -3;
        transition: 0.3s;
    }
    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
        padding: 1rem 0;

    }
    .nav-menu li a{
        font-size:2rem;

    }
    .hamburger{
        display:initial;
    }
}